.ReviewsList {
  list-style: none;
}

.ButtonToTop {
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
  width: 28px;
  height: 28px;
  border: 0;
  background-image: url("https://cdn3.iconfinder.com/data/icons/faticons/32/arrow-up-01-512.png");
  background-size: 90%;
  background-repeat: no-repeat;
  background-position: center;
  color: white;
  cursor: pointer;
  border-radius: 10px;
  font-size: 18px;
}

.ButtonToTop:hover {
  background-color: orange;
}
